<template>
    <div>
        <basic-header :items="['用户管理','用户管理']"></basic-header>
        <el-card>
            <el-tabs v-model="activeName">
                <el-tab-pane label="用户查看" name="first">
                    <el-form :inline="true" class="demo-form-inline">
                        <el-form-item label="名称">
                            <el-input v-model="searchForm.nickName" :clearable="true"></el-input>
                        </el-form-item>
                        <el-form-item>
                            <el-button type="primary" icon="el-icon-search" circle @click="search"></el-button>
                        </el-form-item>
                    </el-form>
                    <el-table :data="tableData" border style="width: 100%">
                        <el-table-column label="ID"></el-table-column>
                        <el-table-column label="用户识别ID"></el-table-column>
                        <el-table-column label="昵称"></el-table-column>
                        <el-table-column label="头像">
                            <template #default="o">
                                <img src="o.row.thumb" alt="" height="100px">
                            </template>
                        </el-table-column>
                        <el-table-column label="性别"></el-table-column>
                        <el-table-column label="所在地区"></el-table-column>
                        <el-table-column label="创建时间"></el-table-column>
                        <el-table-column label="修改时间"></el-table-column>
                        <el-table-column label="操作">
                            <template #default="o">
                                <el-button type="text" @click="">修改</el-button>
                            </template>
                        </el-table-column>
                    </el-table>
                </el-tab-pane>
                <el-tab-pane label="添加用户" name="second">
                </el-tab-pane>
            </el-tabs>
        </el-card>
    </div>
</template>

<script>
    import Header from "../../components/Header";
    export default {

        data() {
            return {
                activeName:"first",
                tableData:[],
                page:1,
                pageSize:5,
                count:10,
                searchForm:{
                    nickName:"",
                },
            }
        },
        methods: {
            fetchData(){
                let params={
                    page:this.page,
                    pageSize:this.pageSize,
                };
                if (this.searchForm.nickName){
                    params.nickName=this.searchForm.nickName
                }
                this.axios.get("../api/users/users",{
                    params
                }).then(res=>{
                    this.tableData=res.data.data;
                    this.count=res.data.total;
                })
            },
            search(){
                this.fetchData();
                this.page=1;
            }
        },
        components:{
            "basic-header":Header
        },
        created() {
            this.fetchData()
        },
        mounted() {

        }
    }
</script>

<style scoped>

</style>